* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
  font-family: Arial, Helvetica, sans-serif;
}

:root {
  --light-color: #00cc99;
  --normal-color: #009973;
  --dark-color: #00664d;
}

/* ///////////////////// navbar //////////////////////// */

.navbar {
  display: flex;
  justify-content: space-between;
  padding: 20px;
  background-color: var(--normal-color);
  position: sticky;
  top: 0;
  box-shadow: black 0px 3px 8px;
}

.resume {
  color: black;
  text-decoration: none;
  background-color: white;
  border: none;
  padding: 5px 10px;
  border-radius: 5px;
}

.resume:hover {
  background-color: black;
  color: white;
}

@media only screen and (min-width: 600px) {
  .menu {
    color: white;
    text-decoration: none;
    padding: 5px 10px;
    border-radius: 5px;
  }

  .menu:hover {
    background-color: var(--light-color);
    color: black;
  }

  .menu-icon {
    display: none;
  }
}

@media only screen and (max-width: 600px) {
  .menu-icon {
    border: 1px solid white;
    width: 38px;
    position: fixed;
    top: 10px;
    left: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 5px;
    cursor: pointer;
  }

  .menu-icon > img {
    width: 100%;
  }

  .menu-link {
    width: 150px;
    display: grid;
    position: fixed;
    top: 60px;
    left: 60px;
    background-color: var(--normal-color);
    padding: 20px;
    box-shadow: black 0px 1px 4px;
  }

  .menu {
    color: white;
    text-decoration: none;
    padding: 5px;
    border-radius: 5px;
  }

  .menu:hover {
    background-color: var(--light-color);
    color: black;
  }
}

/* /////////////////////// home //////////////////////// */

.home {
  text-align: center;
  padding: 20px;
  margin: 100px 0px 150px 0px;
}

.greeting {
  font-weight: bold;
  margin-bottom: 20px;
}

.my-name {
  font-size: 65px;
  color: var(--normal-color);
  margin-bottom: 20px;
}

.my-segment {
  font-size: x-large;
  margin-bottom: 40px;
}

.about-me-first {
  padding: 0px 10%;
  color: var(--dark-color);
}

/* //////////////////////////// about /////////////////////// */
.about {
  margin-bottom: 150px;
}

.small-heading {
  font-weight: bold;
  text-align: center;
  margin-bottom: 20px;
}

.heading {
  text-align: center;
  font-size: xx-large;
  margin-bottom: 50px;
}

.image-sec > img {
  width: 100%;
  border-radius: 50px;
}

.links > a > img {
  width: 30px;
  margin-right: 10px;
  cursor: pointer;
}

@media only screen and (min-width: 600px) {
  .about-me-sec {
    display: flex;
    justify-content: space-around;
    background-color: var(--normal-color);
    padding: 5% 10%;
  }

  .about-sec {
    width: 50%;
    display: flex;
    align-items: center;
  }

  .about-para {
    margin-bottom: 20px;
    color: white;
    letter-spacing: 0.1em;
  }

  .image-sec {
    width: 25%;
  }
}

@media only screen and (max-width: 600px) {
  .about-me-sec {
    background-color: var(--normal-color);
    padding: 15% 10%;
  }

  .about-para {
    margin-bottom: 20px;
    color: white;
    letter-spacing: 0.1em;
    text-align: center;
  }

  .image-sec {
    margin-bottom: 30px;
  }

  .links {
    text-align: center;
  }
}

/* //////////////////////// skills ////////////////////////// */

.skills {
  margin-bottom: 150px;
}

.skill {
  box-shadow: black 0px 1px 4px;
  text-align: center;
  padding: 25px 0px;
  border-radius: 10px;
}

.skill > img {
  width: 30%;
}

.skill > span {
  color: var(--light-color);
  display: block;
}

@media only screen and (min-width: 600px) {
  .skills-sec {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 50px 80px;
    padding: 0px 10%;
  }
}

@media only screen and (max-width: 600px) {
  .skills-sec {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 50px;
    padding: 0px 15%;
  }
}

/* ////////////////////// projects //////////////////////// */

.projects {
  margin-bottom: 150px;
}

.project {
  background-color: var(--normal-color);
  text-align: center;
  padding: 5%;
  border-radius: 5px;
}

.project > img {
  width: 100%;
  border-radius: 5px;
  margin-bottom: 20px;
}

.project > h3 {
  margin-bottom: 10px;
  color: white;
}

.project > h5 {
  margin-bottom: 10px;
  color: white;
}

.project > p {
  margin-bottom: 20px;
  color: black;
}

.project a {
  color: black;
  text-decoration: none;
  background-color: white;
  padding: 5px 30px;
  margin: 10px;
  border-radius: 5px;
}

.project a:hover {
  background-color: black;
  color: white;
}

@media only screen and (min-width: 600px) {
  .projects-sec {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 30px;
    padding: 0px 5%;
  }
}

@media only screen and (max-width: 600px) {
  .projects-sec {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    gap: 20px 0px;
    padding: 0px 5%;
  }
}

/* //////////////////// statistics ///////////////////////// */

.statistics {
  margin-bottom: 150px;
}

.git-sec p {
  text-align: center;
  width: 100%;
}

.git-trophy > img {
  width: 80%;
}

.git-stats > p,
.git-trophy {
  margin-bottom: 20px;
}

.git-cal > img {
  width: 80%;
}

@media only screen and (min-width: 600px) {
  .git-stats > p > img {
    width: 50%;
  }
}

@media only screen and (max-width: 600px) {
  .git-stats > p > img {
    width: 80%;
  }
}

/* //////////////////// contact /////////////////////// */

.contact {
  margin-bottom: 150px;
}

.link-sec > a {
  display: block;
  background-color: var(--normal-color);
  text-align: center;
  padding: 10px 0px;
  border-radius: 5px;
  color: black;
  text-decoration: none;
}

.link-sec > a:hover {
  background-color: var(--light-color);
}

.link-sec > a > p {
  font-size: small;
  
}

.link-sec > a > img {
  width: 30px;
}

.form-sec > input,
textarea {
  border: 2px solid var(--normal-color);
  padding: 15px;
  border-radius: 5px;
  margin-bottom: 20px;
  width: 100%;
  outline: none;
}

.form-sec > button {
  background-color: var(--normal-color);
  border: none;
  border-radius: 5px;
  padding: 10px 15px;
  font-weight: bold;
  cursor: pointer;
  color: white;
}

.form-sec > button:hover {
  background-color: var(--dark-color);
}

@media only screen and (min-width: 600px) {
  .contact-sec {
    display: flex;
    justify-content: center;
  }

  .link-sec {
    margin-right: 20px;
    width: 20%;
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    gap: 20px 0px;
  }

  .form-sec {
    width: 40%;
  }
}

@media only screen and (max-width: 600px) {
  .contact-sec {
    padding: 0px 10%;
  }

  .link-sec {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    gap: 20px 0px;
    margin-bottom: 20px;
  }
}

/* /////////////////// footer //////////////////////// */

.footer {
  background-color: var(--normal-color);
  text-align: center;
  padding: 30px 0px;
}

.footer > h3 {
  color: white;
  margin-bottom: 20px;
}
